body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

header {
  color: #fff;
  text-align: center;
  background-color: #4caf50;
  padding: 20px;
}

header h1 {
  margin: 0;
}

main {
  padding: 20px;
}

section {
  margin-bottom: 30px;
}

h2 {
  color: #333;
  margin-bottom: 10px;
}

input[type="number"], input[type="text"] {
  border: 1px solid #ccc;
  border-radius: 4px;
  width: calc(100% - 20px);
  margin: 10px 0;
  padding: 10px;
  font-size: 16px;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #4caf50;
  border: none;
  border-radius: 4px;
  padding: 10px 20px;
  font-size: 16px;
}

button:hover {
  background-color: #45a049;
}

#chatbot-box {
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 8px;
  margin-top: 40px;
  padding: 20px;
  display: block;
  box-shadow: 0 4px 8px #0000001a;
}

#chat-history {
  border-bottom: 1px solid #ccc;
  max-height: 300px;
  padding: 10px;
  overflow-y: auto;
}

#chat-history p {
  background-color: #f1f1f1;
  border-radius: 4px;
  margin: 5px 0;
  padding: 5px;
}

#chat-input {
  border: 1px solid #ccc;
  border-radius: 4px;
  width: calc(100% - 90px);
  margin: 10px 0;
  padding: 10px;
}

#send-btn {
  color: #fff;
  cursor: pointer;
  background-color: #4caf50;
  border: none;
  border-radius: 4px;
  padding: 10px 20px;
}

#send-btn:hover {
  background-color: #45a049;
}

section p {
  font-size: 16px;
}

ul {
  padding: 0;
  list-style-type: none;
}

ul li {
  background-color: #f9f9f9;
  border-radius: 4px;
  margin: 10px 0;
  padding: 8px;
}
